import {request} from './request';

export function addAddress(params) {
  return request({
    url: '/api/address',
    method: 'POST',
    params
  })
}

export function editAddress(id,params) {
  return request({
    url: `/api/address/${id}`,
    method: 'PUT',
    params
  })
}

export function deleteAddress(id) {
  return request({
    url: `/api/address/${id}`,
    method: 'DELETE',
  })
}

export function getAddresslist() {
  return request({
    url: '/api/address',
  })
}

export function getAddressDetail(id) {
  return request({
    url: `/api/address/${id}`,
  })
}


